import React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { createTheme } from '@mui/material/styles';
import Ailerons from './fonts/Ailerons.woff';
import EnchantedAmulet from './/images/enchantedamulet.jpeg';
import { ThemeProvider } from "@mui/material/styles";

const theme = createTheme({
  typography: {
    fontFamily: 'Ailerons',
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: `
        @font-face {
          font-family: 'Ailerons';
          font-style: normal;
          font-display: swap;
          font-weight: 200;
          src: local('Raleway'), local('Raleway-Regular'), url(${Ailerons}) format('woff2');
          unicodeRange: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF;
        }
      `,
    },
  },
});

const styles = {
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9,
    marginTop:'0'
  }
};
function MediaCard() {
  return (
    <Card sx={{ maxWidth: 425, minWidth:425 }}>
      <CardMedia
        sx={{ height: 200}}
        image={EnchantedAmulet} 
        title="enchanted amulet"
        style={styles.media}
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          Enchanted Amulet
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Powerful Amulet capable of bending space and time. 
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Purchase</Button>
        <Button size="small">Learn More</Button>
      </CardActions>
    </Card>
  );
}


// const Item = styled(Paper)(({ theme }) => ({
//   backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
//   ...theme.typography.body2,
//   padding: theme.spacing(1),
//   textAlign: 'center',
//   color: theme.palette.text.secondary,
// }));

const FeaturedItems = () => {
  return (
    <div>
      <ThemeProvider theme={theme}>
        <div style={{margin:'0 auto',width: '95%'}}>
      <Box alignItems="center" justifyContent="center" display="flex" margin='auto' width={{xs:"100%",sm:"80%",md:'85%',lg:'95%',xl:'98%'}}>
        <Grid container spacing={{xs:'1px',sm:'5px',md:'5px'}}>
          <Grid item xs={12} sm={12} md={6} lg={4} xl={3}>
            <MediaCard></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={6} lg={4} xl={3}>
            <MediaCard></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={6} lg={4} xl={3}>
            <MediaCard></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={6} lg={4} xl={3}>
            <MediaCard></MediaCard>
          </Grid>
        </Grid>
      </Box>
      </div>
      </ThemeProvider>
    </div>
  );
};
export default FeaturedItems;